import React, { useState } from "react";
import './Login.css'
import Change from './pictures/change.png'
import { Button, Divider, Drawer, Input, Select, ConfigProvider } from 'antd';
import { CloseOutlined, CaretDownOutlined, ExclamationCircleOutlined } from '@ant-design/icons';

const options = [
  {
    label: '+86 ',
    value: '+86',
  },
  {
    label: '+1 ',
    value: '+1',
  },
  {
    label: '+2 ',
    value: '+2',
  },
  {
    label: '+852 ',
    value: '+852',
  },
];
function App() {
  const [phoneError, setPhoneError] = useState(false)
  const verification = () => {
    const phoneNumber = /^1[3456789]\d{9}$/;
    if (!phoneNumber.test(mobile)) {
      setPhoneError(<><ExclamationCircleOutlined /> 11</>);
    } else {
      setPhoneError(null);
    }
  };
  const [isFocus, setIsFocus] = useState(false)
  const [mobile, setMobile] = useState()
  //
  const [open, setOpen] = useState(false);
  //
  const showDrawer = () => {
    setOpen(true);
  };
  //
  const onClose = () => {
    setOpen(false);
    
  };
  const toggleDrawer = () => {
    setOpen(!open);
  };
  return (
    <>
      <div className="picture-background">
        <span className="meetingpd">
          MEETINGPAD
        </span>
        {/*  */}
        <div onClick={toggleDrawer}>
          <span className="rectangle" >
            <span className="plus">
              +
            </span>
            <span className="add">
              
            </span>
          </span>
        </div>
        <span className="balck-square ">
          <span className="text-square">
             PDF 100 MB10
          </span>
        </span>
        <span className="rectangle-right" style={{ zIndex: 99999 }}>
          <img src={Change} alt={''} className="language-change " />
          {/*  */}
          <div className="background" onClick={onClose}>
          </div>
          <Divider type="vertical" style={{ marginRight: 110 }} />
          <div>
            <span className="console" >
              
            </span>
            <span className="registration" onClick={showDrawer}>/</span>
          </div>
          {/*  */}
          {open && <CloseOutlined style={{ marginLeft: 75 }} onClick={onClose} />}
        </span>
        <Drawer
          mask={false} 
          onClose={onClose}
          open={open}
          width='600px'
          // 
          closeIcon={false}
        >
          <div className="drawer">
            <div className="text1-drawer text">/</div>
            <div className="text2-drawer">/</div>
            <div>
              {/*  */}
              <span className={`text-number ${isFocus ? 'text-focus' : 'text-none'} ${phoneError ? 'text-error' : ''}`}></span>
              <ConfigProvider theme={{ token: { colorPrimary: '#F0C54F' } }} >
                <Input
                  className={`Input ${phoneError ? 'input-error' : ''}  ${mobile ? 'input-blur' : ''}`}
                  onFocus={() => { setIsFocus(true); setPhoneError(null); }}
                  //
                  onChange={(e) => setMobile(e.target.value)}
                  //
                  onBlur={() => !!mobile || setIsFocus(false)}
                  prefix={
                      <Select
                        variant="borderless"
                        options={options}
                        width="60"
                        defaultValue='+86'
                        optionLabelProp="value"
                        popupMatchSelectWidth={false}
                        defaultActiveFirstOption='true'
                        suffixIcon={<CaretDownOutlined />}
                        onClick={(e) => {
                          e.preventDefault();
                          e.stopPropagation();
                        }}
                      />
                    } >
                </Input>
              </ConfigProvider>
            </div>
            {phoneError && <div className="error-text" >{phoneError}</div>}
            <Button className="Button" onClick={verification}></Button>
            <div className="text3-drawer">   </div>
          </div>
        </Drawer>
        <span className="text-bottom">
               ICP2022103850-1  35020602002560V1.3
        </span>
      </div>
    </>
  );
}
export default App;